var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page-container"},[_c('el-breadcrumb',[_c('el-breadcrumb-item',{attrs:{"to":{ path: '/' }}},[_vm._v("首頁")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("服務項目")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("美麗重現")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("隆鼻")])],1),_vm._ssrNode(" "),_c('Title',{attrs:{"title":"隆鼻","titleEn":"RHINOPLASTY"}}),_vm._ssrNode(" <div class=\"article-container service-page\"><img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/rhinoplasty/banner.jpg")))+" alt=\"隆鼻 Banner\"></div> <div class=\"article-container\"><h2>美麗功效</h2> <p>方人因先天因素，五官相對於西方人較缺乏立體感，加上東方人深信高挺的鼻子對於運勢是有幫助的，因此隆鼻手術不分男女諮詢度總是居高不下。隆鼻是目前受到廣泛歡迎的一種整形美容方式，隆鼻美鼻對於面部形象可以有效的改善求美者的鼻子形態，讓求美者的鼻子變得俏麗迷人。</p></div> <div class=\"article-container\"><h2>黃金比例</h2> <p>目前公認的東方美女標準臉型，長寬比例符合“黃金分割”，額頭飽滿，面頰棱角柔美,下顎圓潤流暢,角度為120度，顱頂至鼻根與鼻根至下巴等長，髮際至眉心、眉心至鼻下、鼻下至下頜等距。鼻尖-嘴唇-顎下點基本為一直線。</p> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/rhinoplasty/1.jpg")))+" alt=\"黃金比例示意圖\"></div> <div class=\"article-container\"><h2>一般傳統隆鼻手術的人工鼻骨植入手術方法：</h2> <ul><li>+ 在鼻孔內作一小切口，選擇植入L型或I型人工鼻模，把鼻樑和鼻尖墊高。此種隆鼻方法看不見傷口，手術傷口隱藏在鼻孔內，局部麻醉即可。</li> <li>+ 採掀開式隆鼻手術，將鼻頭掀開，把人工鼻骨放入，此種方式傷口較大，但可處理的問題較多。</li></ul> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/rhinoplasty/2.jpg")))+" alt=\"一般傳統隆鼻手術的人工鼻骨植入手術方法\"></div> <div class=\"article-container\"><h3>隆鼻材質主要分為兩大種類：</h3> <ol><li>人工合成植入物</li> <li>人體組織</li></ol></div> <div class=\"article-container\"><h2>人工合成植入物</h2> <p>材質穩定，是早期最被廣泛運用的材料，尤其是L型的矽膠在台灣更是大量被使用。目前市面上的矽膠可分為L 型與I 型矽膠兩大類。</p> <h3>L型矽膠隆鼻的優點：</h3> <ul><li>+ 手術迅速，約30分鐘可完成。</li> <li>+ L型的轉折，可以撐出鼻頭手術後若不滿意，再次手術取出容易。</li></ul> <h3>I型矽膠隆鼻的優點：</h3> <ul><li>+ 手術快速，搭配使用自體耳軟骨或鼻中膈軟骨來墊鼻頭，約60-90分鐘可完成。</li> <li>+ 只用矽膠來墊鼻樑，皮膚施加於矽膠植入物的壓力較小，位移破出的機率較低。</li> <li>+ 手術後若不滿意，再次手術取出容易。</li></ul> <h3>人工合成植入物-卡麥拉隆鼻的優點：</h3> <ul><li>+ 一般認為可以同時合併有矽膠較硬的特性與Gore-tex較不會產生莢膜反應的優點。</li> <li>+ 觸感較自然與人體的相容性高。</li> <li>+ 不易位移</li></ul></div> <div class=\"article-container\"><h2>人體組織</h2> <h3>異體肋軟骨優點：</h3> <ul><li>+ 質地堅硬，可以雕刻出優美弧線的鼻樑與撐出自然好看的鼻頭。</li> <li>+ 手術時間較短(因為不需花時間取自體肋骨)。</li> <li>+ 不須在胸部多一道2.5-3公分的傷口</li> <li>+ 術後不會有胸部傷口疼痛問題。</li> <li>+ 幾乎不會有排斥、感染、位移等併發症。 人體組織-自體軟骨優點</li> <li>+ 吸收率較異體軟骨低。</li> <li>+ 取用於自身，不必花大錢買別人的肋骨。</li> <li>+ 完全不會有感染、排斥、位移等併發症。</li> <li>+ 可應用於各種鼻功能與鼻外觀矯正(塌鼻、蒜頭鼻、歪鼻、朝天鼻、鷹勾鼻、鼻塞…等等)。</li> <li>+ 手術成果美觀自然，較不會有”鼻子看起來很假”的感覺。 ●可以根據病人需求客製化適合的鼻子。</li></ul></div> <div class=\"article-container\"><h2>適用對象</h2> <ul><li>+ 想改善鼻型，如扁鼻、朝天鼻、鷹勾鼻、鼻翼過大等問題者。</li> <li>+ 能自選隆鼻效果的維持時間，就算想移除也僅需3～5分鐘者。</li> <li>+ 希望手術時間短的患者。</li> <li>+ 對隆鼻手術有恐懼不敢嘗試的患者。</li> <li>+ 已做過隆鼻手術且留有創傷者，或對隆鼻效果不滿意的患者。</li></ul></div> <div class=\"article-container\"><h2>術後小叮嚀</h2> <ul><li>+ 術後一周內，為防止鼻子變形，病患應小心避免任意碰撞、重壓鼻部術後會有輕微腫脹感，約2～4周逐漸消腫。</li> <li>+ 因體質差異性，少數人會有瘀青現象，手術後 48小時內需採冰敷方式，每次冰敷15分鐘後休息45分鐘再敷，一日數次。</li> <li>+ 48小時後再改溫熱敷方式，每次溫熱敷15分鐘後休息45分鐘再敷，一日數次直到腫脹感少。</li> <li>+ 微創傷口保持乾淨以防感染。術後可正常洗臉，傷口如遇水時需用生理食鹽水清潔，再用無菌棉棒拭乾後擦上藥膏。</li> <li>+ 手術後一星期內，睡覺時可將枕頭墊高，使腫脹情形減輕。</li> <li>+ 手術後一周開始，需做1~2秒的定期按摩，以維持效果</li> <li>+ 手術後二星期內，請避免擠粉刺或挖鼻口及擤鼻涕，並避免帶眼鏡、大力撞擊、趴睡。</li> <li>+ 手術後二星期內，請避免辛辣、菸、酒等剌激性的食物，切勿吸煙。</li> <li>+ 若有異常出血與血腫異常疼痛出現時，請諮詢或回診。</li> <li>+ 確實遵照護理人員術後衛教方式照顧微創傷口。</li> <li>+ 臉部或全身有感染(如有癤腫或毛囊炎)、鼻部皮脂腺豐富或有酒糟鼻、正值懷孕及哺乳期間、未成年骨骼尚未發育完畢者皆不適合此療程。</li> <li><h4>本療程的效果可能因個人體質的不同而有差異，請務必與醫師溝通諮詢</h4></li></ul></div> <div class=\"article-container\"><h2>美麗案例</h2> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/rhinoplasty/案例1.png")))+" alt=\"案例1\n        \"> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/rhinoplasty/案例2.png")))+" alt=\"案例2\n        \"> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/rhinoplasty/案例3.png")))+" alt=\"案例3\n        \"> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/rhinoplasty/案例4.png")))+" alt=\"案例4\n        \"> <h4>療程效果會因個人體質而有所差異</h4></div>")],2)}
